.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(180deg, #1a1b2e 0%, #2d1b3d 100%);
  color: white;
  padding: 10px;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 10px;
}

.message {
  margin-top: 20px;
  font-size: 16px;
  opacity: 0.8;
  padding: 0 20px;
}

@media (max-width: 480px) {
  .App {
    padding: 5px;
  }
  
  .container {
    padding: 5px;
  }
  
  .message {
    margin-top: 15px;
    font-size: 14px;
  }
}
