.timer {
  margin: 20px 0;
}

.timer-display {
  font-size: 72px;
  font-weight: 300;
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.05);
}

@media (max-width: 480px) {
  .timer {
    margin: 10px 0;
  }

  .timer-display {
    font-size: 48px;
    width: 150px;
    height: 150px;
  }
} 