.sound-button-container {
  position: relative;
  width: 100%;
}

.sound-button {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 10px;
  padding: 20px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  aspect-ratio: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.sound-button:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-2px);
}

.sound-button.playing {
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
}

.sound-icon {
  font-size: 24px;
  margin-bottom: 5px;
}

.sound-name {
  font-size: 14px;
  opacity: 0.8;
}

.volume-slider-container {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  background: rgba(255, 255, 255, 0.1);
  padding: 5px;
  border-radius: 10px;
  z-index: 1;
}

.volume-slider {
  width: 100%;
  -webkit-appearance: none;
  height: 3px;
  background: rgba(255, 255, 255, 0.3);
  outline: none;
  border-radius: 3px;
}

.volume-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  background: white;
  border-radius: 50%;
  cursor: pointer;
}

.volume-slider::-moz-range-thumb {
  width: 15px;
  height: 15px;
  background: white;
  border-radius: 50%;
  cursor: pointer;
  border: none;
}

@media (max-width: 480px) {
  .sound-button {
    padding: 15px;
    gap: 5px;
  }

  .sound-icon {
    font-size: 20px;
    margin-bottom: 3px;
  }

  .sound-name {
    font-size: 12px;
  }

  .volume-slider-container {
    bottom: -25px;
  }

  .volume-slider::-webkit-slider-thumb {
    width: 12px;
    height: 12px;
  }

  .volume-slider::-moz-range-thumb {
    width: 12px;
    height: 12px;
  }
} 